import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

/**
 * @class LocalStockage - Ce service permet d'utiliser le stockage local de l'appareil.
 */

@Injectable()
export class LocalStockage {

  constructor(public storage: Storage) { }

  /**
   * Mthode qui permet d'enregistrer des donnes localement. les donnes sont enregistres par paire cl/valeur.
   * @method setData
   * @param {Objet} - un objet est pass  la mthode qui enregistre indpendemment chacune de ses proprits.
   * @returns {Promise} - une promesse est renvoye qui se termine lorsque l'ensemble des donnes a t enregistr. 
   */
  setData(data) {
    return new Promise((resolve,reject) => {
      //Dcomposition des proprits de l'objet en paire cl/valeur
      for(var propertyName in data) {
        console.log(propertyName + ' en cours d\'enregistrement : ' + data[propertyName]);
        this.storage.set(propertyName,data[propertyName]);//Enregistrement de la paire cl/valeur
      }
      resolve('enregistr !');
    });
  }

  /**
   * Mthode qui rcupre une donne stocke localement  partir de sa cl.
   * @method getData 
   * @param {string} - le nom de la cl identifiant la donne stocke.
   * @returns {promise} - une promesse est renvoye qui se termine lorsque la donne est rcupre. 
   */
  getData(data){
      return this.storage.get(data);
  }

  /**
   * Mthode qui permet de supprimer des donnes stockes localement. Seul l'identifiant d'un formulaire n'est pas supprim, et les donnes des autres formulaires non envoys sur le rseau.
   * @method removeData 
   * @param {Objet} - l'objet dont les valeurs des proprits doivent tre supprimes.
   * @returns {Promise} - une promesse est renvoye qui se termine lorsque les donnes sont supprimes. 
   */
  removeData(data){
    return new Promise((resolve, reject) => {
      for(var propertyName in data) {
        //L'identifiant unique, qui peut tre une des proprits de l'objet data, n'est pas supprim.
        if (propertyName!="idForm" && !propertyName.startsWith('Saved_Form') && propertyName!="setCenter"){
          this.storage.remove(propertyName).then(() => {
            console.log(propertyName + ' supprime');
          });
        }
      }
      resolve('Supression des donnes');
    });
  }

  /**
   * Mthode qui rcupre l'ensemble des donnes stockes localement.
   * @method getAllData
   * @param {} - aucun paramtre n'est pass  la mthode.
   * @returns {Promise} - une promesse est renvoye avec les valeurs des donns stockes sous la forme d'un objet. 
   */
  getAllData(){
    let dataStorage = {};
    return new Promise((resolve, reject) => {
      this.storage.forEach((value, key, index) => {
        if (typeof key === 'string' && key.endsWith('Form')){
          dataStorage[key]=value;
          //console.log('la valeur est ' + value + ' et la key est ' + key);
        }
      }).then(() => {      
        resolve(dataStorage);
      });
    }); 
  }

  /**
   * Mthode qui permet de supprimer des donnes stockes localement. Seules les donnes des autres formulaires non envoys sur le rseau ne sont pas supprimes.
   * @method clearAllData 
   * @param {} - l'objet dont les valeurs des proprits doivent tre supprimes.
   * @returns {Promise} - une promesse est renvoye qui se termine lorsque les donnes sont supprimes. 
   */
  clearAllData(){
    return new Promise((resolve, reject) => {
      this.storage.forEach( (value, key, index) => {
        if (!key.startsWith('Saved_Form') && key !== 'setCenter'){
          this.storage.remove(key).then(() => {
            //console.log(key + ' supprime : ' + value);
          });
        }
      }).then(() => {      
        resolve('Supression des donnes');
      });
    });
  }

  /**
   * Mthode qui enregistre sous un nom particulier les donnes qui n'ont pas pu tre envoyes sur le serveur, pour une prohcaine connexion.
   * @method storeAllData
   * @param {Objet} - l'objet dont les valeurs des proprits doivent tre enregistres.
   * @returns {Promise} - une promesse est renvoye lorsque toutes les donnes ont t enregistres.
   */
  storeAllData(data){
    var currentTime = new Date();
    let key = 'Saved_Form' + String(currentTime);
    return this.storage.set(key,JSON.stringify(data));
  }

  /**
   * Mthode qui rcupre l'ensemble des donnes qui n'ont pas t envoyes sur le serveur mais qui ont t stockes localement.
   * @method getStoreData
   * @param {} - aucun paramtre n'est pass  la mthode.
   * @returns {Promise} - une promesse est renvoye avec les valeurs des donns stockes sous la forme d'un objet. 
   */
  getStoreData(){
    var dataStorage = {};
    return new Promise((resolve, reject) => {
      this.storage.forEach( (value, key, index) => {
        if (key.startsWith('Saved_Form')){
          dataStorage[key] = value;
          console.log(key + ' rcupre : ' + value);
        }
      }).then(() => {
        resolve(dataStorage);
      });
    }); 
  }

  /**
   * Mthode qui supprime une paire key/value stocke
   * @method clearData
   * @param {string} - la key de la paire  supprimer est passe  la mthode.
   * @returns {Promise} - une promesse est renvoye lorsque la donne a t supprime. 
   */
  clearData(key){
    return this.storage.remove(key).then(() => {
      //console.log(key + ' supprime');
    });
  }
}


