import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

import { Api } from './api';
import { LocalStockage } from './localstockage';

/**
 * @class Formulaire - Ce service utilise les requtes dfinies dans le fichier providers/api pour faire l'interface entre le client et le serveur.
 * Les rponses du serveur aux requtes envoyes sont des objets JSON, et doivent commencer par le champ `status` : 
 * ```json
 * {
 *   status: 'success',
 *   formres: {
 *     // ce champ doit contenir a minima l'id du formulaire, stock sous le nom idForm.
 *   }
 * }
 * ```
 * Si le champ `status` n'est pas un `success`, une erreur est envoye.
 */

@Injectable()
export class Formulaire {

  public subCreate: any;

  constructor(public http: Http, public api: Api, public localstockage: LocalStockage) { }

  /**
   * Mthode qui envoie une requte POST pour crer un nouveau formulaire ct serveur. 
   * La cration d'un nouveau formulaire s'accompagne par la cration d'un identifiant unique ct serveur, renvoy dans la rponse du serveur.
   * @method createForm
   * @requires providers/localstockage - la fonction utilise les mthodes setData, removeData.
   * @requires providers/api - la fonction utilise la mthode post.
   * @param {Objet} - un objet est pass  la mthode qui va envoyer chacune des proprits de l'objet au serveur.
   * @returns {Observable} - un observable est renvoy pour suivre l'tat de la requte. 
   */
  createForm(dataForm) {
    //On ne peut crer qu'un seul formulaire ct serveur. Il faut s'assurer qu'il n'y a dj pas une requte en cours lorsqu'on envoie une requte de cration du formulaire.
    if(this.subCreate) {
       this.subCreate.unsubscribe();
    }
    let seq = this.api.post('newForm/', dataForm).share();
    interface idObjet { idForm: number };
    this.subCreate = seq.map(res => res.json())
      .subscribe(res => {
        // Si la requte est un succs, l'identifiant du formulaire est stock localement
        if (res.status == 'success') {
          var idCreaForm: idObjet = {idForm : res.data}; // Le stockage de l'identifiant du formulaire doit avoir le nom idForm.
          this.localstockage.setData(idCreaForm);
          this.localstockage.removeData(dataForm);//Il faut ensuite supprimer toutes les donnes qui ont t enregistres sur le serveur, sauf l'identifiant du formulaire.
        }
      }, err => {
        console.error('ERROR', err);
      });
    return seq;
  }

  /**
   * Mthode qui envoie une requte PATCH pour mettre  jour le formulaire ct serveur. 
   * @method updateForm
   * @requires providers/localstockage - la fonction utilise la mthode removeData.
   * @requires providers/api - la fonction utilise la mthode patch.
   * @param {any, Objet} - une variable avec la valeur de l'identifiant du formulaire et les donnes  mettre  jour, sous la forme d'un objet, sont passes  la mthode.
   * @returns {Observable} - un observable est renvoy pour suivre l'tat de la requte. 
   */
  updateForm(dataForm) { 
    //On doit s'assurer que les donnes mises  jour soient mise  jour dans le bon ordre : il est prfrable d'annuler toute requte dj existante.
    if(this.subCreate) {
       this.subCreate.unsubscribe();
    }
    
    let seq = this.api.patch('updateform/', dataForm).share();

    this.subCreate = seq.map(res => res.json())
      .subscribe(res => {
        if (res.status == 'success') {
          this.localstockage.removeData(dataForm);//Il faut ensuite supprimer toutes les donnes qui ont t enregistres sur le serveur, sauf l'identifiant du formulaire.
        }
      }, err => {
        console.error('ERROR', err);
      });

    return seq;
  }

  /**
   * Mthode qui envoie une requte DELETE pour supprimer un formulaire ct serveur. 
   * @method removeForm
   * @requires providers/api - la fonction utilise la mthode delete.
   * @param {number} - l'identifiant du formulaire  supprimer.
   * @returns {Observable} - un observable est renvoy pour suivre l'tat de la requte. 
   */
  removeForm(idForm) { 
    //On doit annuler toute requte dj existante.
    if(this.subCreate) {
       this.subCreate.unsubscribe();
    }
    
    let seq = this.api.delete('removeform/' + idForm).share();

    this.subCreate = seq.map(res => res.json())
      .subscribe(res => {}, err => {
        console.error('ERROR', err);
      });

    return seq;
  }
}


