import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Component, OnInit, ViewChild, NgZone } from '@angular/core';
import { NavController, ModalController, Content } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { Keyboard } from '@ionic-native/keyboard';

//Page suivante
import { TherapiesAlter } from '../therapies-alter/therapies-alter';
//Page d'autocompletion
import{ Autocomplete } from '../../autocomplete/autocomplete';

//Providers
import { Formulaire } from '../../../providers/formulaire';
import { LocalStockage } from '../../../providers/localstockage';
import { Traitement } from '../../../providers/traitement';
import { Diacritics } from '../../../providers/diacritics';
import { Inactif } from '../../../providers/inactif';

@Component({
  selector: 'therapies',
  templateUrl: 'therapies.html'
})
export class Therapies implements OnInit {

  @ViewChild(Content) content: Content;

  therapiesForm: FormGroup;
  anticancerForm: FormGroup;
  anticancerTable = [];
  choixTable = [];
  checkTraitement: boolean = false;
  nbTraitement: number = 0;
  submitAttempt: boolean = false;
  traitementNom: any;
  traitementElement: any;
  traitementTitre: string;
  traitementPlaceholder: string;
  showScrollFabTherapies: boolean = false;
  contentDimensions: any;
  isTraitement: boolean = false;
  
  constructor(public navCtrl: NavController, public modalCtrl: ModalController, public zone: NgZone, public translate: TranslateService, public formBuilder: FormBuilder, public formulaire: Formulaire, public localstockage: LocalStockage, public traitement: Traitement, public keyboard: Keyboard, public diacritics: Diacritics, public inactif: Inactif) {
    this.therapiesForm = formBuilder.group({
      radioForm:  ['', Validators.required],
      chirurgieForm:  ['', Validators.required],
      gastrectomieForm:  ['', Validators.required]
    });
    this.anticancerForm = formBuilder.group({
      traitementboolForm: ['', Validators.required]
    });
    this.traitementNom = [];
    this.traitementElement = [];
    this.contentDimensions = {};
  }

  ngOnInit(){
    //Rcupration de la liste des traitements anti-cancreux
    this.traitement.makeTraitList(['TTCAN']).then((liste) =>{
      this.traitementNom = liste[0];
      this.traitementElement = liste[1];
    });
  }

  ionViewDidEnter(){
    //Si l'utilisateur est inactif, une alerte est envoye avec la possibilit de continuer ou de recommencer le questionnaire.
    this.inactif.idleSet(this.navCtrl);
    this.contentDimensions = this.content.getContentDimensions();
    if (this.contentDimensions.contentHeight + 50 < this.contentDimensions.scrollHeight) {
      this.showScrollFabTherapies = true;
    }
  }

  ionViewWillLeave(){
    this.inactif.idleStop();
  }

  /**
   * Fonction qui permet d'afficher ou de cahcer le boutton fab.
   * @method displayFab
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  displayFab(){
    this.zone.run(() => {
      this.contentDimensions = this.content.getContentDimensions();
      if (this.contentDimensions.contentHeight + 50 + this.contentDimensions.scrollTop < this.contentDimensions.scrollHeight) {
        this.showScrollFabTherapies = true;
      } else {
        this.showScrollFabTherapies = false;
      }
    });
  }

  /**
   * Fonction qui permet de scroller tout en bas du contenu.
   * @method scrollDownContent
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  scrollDownContent() {
    this.content.scrollToBottom();
  }

  /**
   * Fonction qui cr deux paires nom du traitement/identifiant du traitement et la stocke dans un tableau.
   * @method createTraitObjet
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  createTraitObjet(){
    this.nbTraitement = this.nbTraitement+1;
    interface traitementObjet {
      traitementnom : string,
      traitementid : string,
      traitementnomControl : FormControl,
      traitementidControl : FormControl
    };
    var traitement: traitementObjet;  
    traitement = {
      traitementnom: "traitementnom_"+this.nbTraitement.toString()+"_Form",
      traitementid: "traitementid_"+this.nbTraitement.toString()+"_Form",
      traitementnomControl : new FormControl ('', Validators.compose([ Validators.pattern('([0-9a-zA-Z\\- \'\(\)]*)'), Validators.required])),
      traitementidControl : new FormControl ('', Validators.compose([ Validators.pattern('([0-9]*)')]))
    }
    this.anticancerTable.push(traitement);
  }

  /**
   * Fonction qui cr deux paires traitement choisi/nom du traitement et la stocke dans un tableau.
   * @method createChoixObjet
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  createChoixObjet(){
    interface choixObjet {
      choixTest : boolean,
      choixNom : string
    };
    var traitChoix: choixObjet = {
      choixTest : false,
      choixNom : ''
    }
    this.choixTable.push(traitChoix);
  }

  /**
   * Fonction qui permet d'ajouter un traitement.
   * @method addTrait
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  addTrait() {
    if (this.anticancerForm.valid){
      let i: number = this.anticancerTable.length;
      //Ajout d'un traitement dans le tableau
      this.createTraitObjet();
      this.createChoixObjet();
      // add treatment to the list
      this.anticancerForm.addControl(this.anticancerTable[i].traitementnom, this.anticancerTable[i].traitementnomControl);
      this.anticancerForm.addControl(this.anticancerTable[i].traitementid, this.anticancerTable[i].traitementidControl);
      this.checkTraitement = false;
      this.submitAttempt = false;
      this.showTraitementModal(i);
    } else {
      this.checkTraitement = true;
    }
  }

  /**
   * Fonction qui permet de supprimer un traitement.
   * @method removeTrait
   * @requires providers/localstockage - la fonction utilise la mthode removeData.
   * @param {number} - le numro du traitement  supprimer est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  removeTrait(i: number) {
    // remove phyto treatment from the list
    var suppressionObjet = {}
    suppressionObjet[this.anticancerTable[i].traitementnom] = this.anticancerForm.value[this.anticancerTable[i].traitementnom];
    suppressionObjet[this.anticancerTable[i].traitementid] = this.anticancerForm.value[this.anticancerTable[i].traitementid];
    this.localstockage.removeData(suppressionObjet);
    this.anticancerForm.removeControl(this.anticancerTable[i].traitementnom);
    this.anticancerForm.removeControl(this.anticancerTable[i].traitementid);
    this.anticancerTable.splice(i,1);
    this.choixTable.splice(i,1);
  }

  /**
   * Fonction qui permet l'entre du nom des traitements anti-cancreux.
   * @method traitementOui
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  traitementOui() {
    this.isTraitement = true;
    this.createTraitObjet();
    this.createChoixObjet();
    //Cration du contenu du formulaire anticancerForm (nom et validateur)
    this.anticancerForm.addControl(this.anticancerTable[0].traitementnom, this.anticancerTable[0].traitementnomControl);
    this.anticancerForm.addControl(this.anticancerTable[0].traitementid, this.anticancerTable[0].traitementidControl);
    this.showTraitementModal(0);
    //nouvelles dimensions de la fentre
    this.contentDimensions = this.content.getContentDimensions();
    if (this.contentDimensions.contentHeight + 50 < this.contentDimensions.scrollHeight) {
      this.showScrollFabTherapies = true;
    }
  }

  /**
   * Fonction qui supprime l'entre du nom des traitements anti-cancreux.
   * @method traitementNon
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  traitementNon() {
    this.isTraitement = false;
    let arrayLength: number = this.anticancerTable.length;
    for (var i = 0; i<arrayLength;i++) {
      var suppressionObjet = {}
      suppressionObjet[this.anticancerTable[i].traitementnom] = this.anticancerForm.value[this.anticancerTable[i].traitementnom];
      suppressionObjet[this.anticancerTable[i].traitementid] = this.anticancerForm.value[this.anticancerTable[i].traitementid];
      this.localstockage.removeData(suppressionObjet);
      this.anticancerForm.removeControl(this.anticancerTable[i].traitementnom);
      this.anticancerForm.removeControl(this.anticancerTable[i].traitementid);
    }
    this.anticancerTable = [];
    this.choixTable = [];
    this.checkTraitement = false;
    this.nbTraitement = 0;
  }

  /**
   * Fonction qui est lie au champ "Nom du traitement anti-cancreux" sur la page du formulaire - Therapies.
   * Elle permet d'ouvrir une page modale (pages/autocomplete) qui propose, en fonction des entres de l'utilisateur une liste de noms possibles : autocompletion.
   * @method showTraitementModal
   * @requires pages/autocomplete - elle appelle la page autocomplete.ts.
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  showTraitementModal(i: number){
    this.translate.get('TITRE_MODAL_TRAITEMENT').subscribe(value => {
      this.traitementTitre = value;
    });
    this.translate.get('PLACEHOLDER_MODAL_TRAITEMENT').subscribe(value => {
      this.traitementPlaceholder = value;
    });
    let modal = this.modalCtrl.create(Autocomplete, {entryAutocomplete: this.anticancerTable[i].traitementnomControl.value, dataAutocomplete: this.traitementNom, titreAutocomplete: this.traitementTitre, placeholderAutocomplete: this.traitementPlaceholder});
    modal.onDidDismiss(data => {
      //Vrification que la donne passe existe et n'est pas seulement des espaces
      if (data && data.replace(/\s/g, '').length!=0){
        //Rcupration des donnes de la page autocompletion
        this.choixTable[i].choixTest = true;
        this.choixTable[i].choixNom = data;
        //Comparaison avec la table traitement
        var traitementData = this.traitementElement.find((val)=>{
          let strVal = this.diacritics.replaceDiacritics(val.nom.toLowerCase());
          let strData = this.diacritics.replaceDiacritics(data.toLowerCase());
          if(strVal === strData){
            return val;
          }
        });
        //Attribution de valeurs aux champs du formulaire (traitementnom et traitementid)
        let dataObj =  {};      
        if(traitementData){
          dataObj[this.anticancerTable[i].traitementnom] = traitementData.nom;
          dataObj[this.anticancerTable[i].traitementid] = traitementData.id;
        } else {
          dataObj[this.anticancerTable[i].traitementnom] = data;
          dataObj[this.anticancerTable[i].traitementid] = 0;
        }
        this.anticancerForm.patchValue(dataObj);
      }
    });
    modal.present();
  }

  /**
   * Fonction qui est lie au bouton "Continuer" sur la page du formulaire - Therapies.
   * Elle valide les valeurs entres dans les champs du formulaire et les stocke localement. 
   * Une fois ces valeurs stockes, elle rcupre la valeur stocke correspondant  l'identificant du formulaire. 
   * Si aucun identifiant n'a t stock, elle cr un nouveau formulaire avec toutes les donnes stockes. Sinon, elle met  jour le formulaire avec ces mmes donnes.
   * Elle affiche ensuite la troisime page du formulaire - Traitements Alternatifs.
   * @method nextPage
   * @requires providers/localstockage - la fonction utilise les mthodes setData, getData, getAllData.
   * @requires providers/formulaire - la fonction utilise les mthodes createForm et updateForm.
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  nextPage() {
    this.submitAttempt = true;
    if(this.therapiesForm.valid && this.anticancerForm.valid){

      //Stockage local des donnes remplies dans cette page de formulaire
      this.localstockage.setData(this.therapiesForm.value).then((message) => {
        this.localstockage.setData(this.anticancerForm.value).then((message) => {
          //Mise  jour/cration du formulaire sur le serveur avec les donnes entres sur cette page du formulaire
          this.localstockage.getData("idForm").then((val)=> {
            this.localstockage.getAllData().then((dataForm)=>{
              //il faut crer/mettre  jour le formulaire avec toutes les donnes stockes
              if (val==null){
                //Si le formulaire n'a pas t cr, il faut le crer
                this.formulaire.createForm(dataForm);
              } else {
                //Sinon, il faut le mettre  jour
                this.formulaire.updateForm(dataForm);
              }
            });
          });
          //Navigation  la page du formulaire - Traitements Alternatifs
          this.navCtrl.push(TherapiesAlter);
        });
      });
    }
  }
}


