import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, ViewChild, NgZone } from '@angular/core';
import { NavController, Content } from 'ionic-angular';

import { TranslateService } from '@ngx-translate/core';

import { TraitementNom } from '../traitement-nom/traitement-nom';
import { Aliments } from '../aliments/aliments';

import { Formulaire } from '../../../providers/formulaire';
import { LocalStockage } from '../../../providers/localstockage';
import { Traitement } from '../../../providers/traitement';
import { TherapieValidator } from '../../../providers/validators';
import { Inactif } from '../../../providers/inactif';

@Component({
  selector: 'therapies-alter',
  templateUrl: 'therapies-alter.html'
})
export class TherapiesAlter{

  @ViewChild(Content) content: Content;

  therapiesAlterForm: FormGroup;
  submitAttempt: boolean = false;
  checkAutres: boolean = false;
  traitementListe: any;
  showScrollFabTherapiesAlter: boolean = false;
  contentDimensions: any;
  
  constructor(public navCtrl: NavController, public zone: NgZone, translate: TranslateService, public formBuilder: FormBuilder, public formulaire: Formulaire, public traitement: Traitement, public localstockage: LocalStockage, public inactif: Inactif) {
    this.therapiesAlterForm = formBuilder.group({
        phytoForm: [false],
        aromaForm: [false],
        vitamineForm: [false],
        homeoForm: [false],
        aucunForm: [false],
        inconnuForm: [false],
        autresboolForm: [false],
        autresForm: ['', Validators.pattern('([0-9a-zA-Z\u0153\\- \'\(\)]*)')]
    },{ validator: TherapieValidator.isValid});
    this.contentDimensions = {};
  }

  ngOnInit(){
    this.traitement.makeTraitList(['PHYTO']).then((liste) =>{
      this.traitementListe = liste;
    });
  }

  ionViewDidEnter(){
    //Si l'utilisateur est inactif, une alerte est envoye avec la possibilit de continuer ou de recommencer le questionnaire.
    this.inactif.idleSet(this.navCtrl);
    this.contentDimensions = this.content.getContentDimensions();
    if (this.contentDimensions.contentHeight + 50 < this.contentDimensions.scrollHeight) {
      this.showScrollFabTherapiesAlter = true;
    }
  }

  ionViewWillLeave(){
    this.inactif.idleStop();
  }

  /**
   * Fonction qui permet d'afficher ou de cahcer le boutton fab.
   * @method displayFab
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  displayFab(){
    this.zone.run(() => {
      this.contentDimensions = this.content.getContentDimensions();
      if (this.contentDimensions.contentHeight + 50 + this.contentDimensions.scrollTop < this.contentDimensions.scrollHeight) {
        this.showScrollFabTherapiesAlter = true;
      } else {
        this.showScrollFabTherapiesAlter = false;
      }
    });
  }
  
  /**
   * Fonction qui permet de scroller tout en bas du contenu.
   * @method scrollDownContent
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  scrollDownContent() {
    this.content.scrollToBottom();
  }

  /**
   * Fonction qui permet le dploiement d'un champ permettant d'entrer le nom d'une thrapie alternative, aprs que l'utilisateur ait dit avoir recours  des thrapies alternatives qui ne sont pas listes dans le formulaire.
   * @method autres
   * @param {id} - un identifiant css est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  autres(inputAutres){
    if(this.checkAutres == false){
      this.checkAutres = true;
      this.therapiesAlterForm.controls.aucunForm.setValue(false);
      this.therapiesAlterForm.controls.inconnuForm.setValue(false);
    }else{
      this.checkAutres = false;
      this.therapiesAlterForm.controls.autresboolForm.setValue(false);
      this.therapiesAlterForm.controls.autresForm.setValue('');
      inputAutres.setFocus();
    }
  }
  
  /**
   * Fonction qui supprime les entres des diffrentes thrapies alternatives, aprs que l'utilisateur ait dit ne pas avoir recours  des thrapies alternatives.
   * @method aucun
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  aucun() {
    this.checkAutres = false;
    this.therapiesAlterForm.controls.phytoForm.setValue(false);
    this.therapiesAlterForm.controls.aromaForm.setValue(false);
    this.therapiesAlterForm.controls.vitamineForm.setValue(false);
    this.therapiesAlterForm.controls.homeoForm.setValue(false);
    this.therapiesAlterForm.controls.autresboolForm.setValue(false);
    this.therapiesAlterForm.controls.autresForm.setValue('');
    this.therapiesAlterForm.controls.inconnuForm.setValue(false);
  }

  /**
   * Fonction qui supprime les entres des diffrentes thrapies alternatives, aprs que l'utilisateur ait dit ne pas savoir s'il a recours  des thrapies alternatives.
   * @method inconnu
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  inconnu() {
    this.checkAutres = false;
    this.therapiesAlterForm.controls.phytoForm.setValue(false);
    this.therapiesAlterForm.controls.aromaForm.setValue(false);
    this.therapiesAlterForm.controls.vitamineForm.setValue(false);
    this.therapiesAlterForm.controls.homeoForm.setValue(false);
    this.therapiesAlterForm.controls.autresboolForm.setValue(false);
    this.therapiesAlterForm.controls.autresForm.setValue('');
    this.therapiesAlterForm.controls.aucunForm.setValue(false);
  }

  /**
   * Fonction qui supprime l'entre de "aucune", aprs que l'utilisateur ait dit avoir recours  des thrapies alternatives.
   * @method therapie
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  therapie() {
    this.therapiesAlterForm.controls.aucunForm.setValue(false);
    this.therapiesAlterForm.controls.inconnuForm.setValue(false);
  }

  /**
   * Fonction qui est lie au bouton "Continuer" sur page du formulaire - Thrapies Alternatives.
   * Elle valide les valeurs entres dans les champs du formulaire et les stocke localement. 
   * Une fois ces valeurs stockes, elle rcupre la valeur stocke correspondant  l'identificant du formulaire. 
   * Si aucun identifiant n'a t stock, elle cr un nouveau formulaire avec toutes les donnes stockes. Sinon, elle met  jour le formulaire avec ces mmes donnes.
   * Elle affiche ensuite la page du formulaire relative aux noms des thrapies si l'utilisateur utilise au moins une thrapie alternative - Nom des Thrapies. Sinon, elle affiche ensuite la page relative aux informations gnrales - Informations Gnrales.
   * @method nextPage
   * @requires providers/localstockage - la fonction utilise les mthodes setData, getData, getAllData.
   * @requires providers/formulaire - la fonction utilise les mthodes createForm et updateForm.
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  nextPage() {
    this.submitAttempt = true;
    if(this.therapiesAlterForm.valid){
      //Stockage local des donnes remplies dans cette page de formulaire
      this.localstockage.setData(this.therapiesAlterForm.value).then((message) => {
        //Mise  jour/cration du formulaire sur le serveur avec les donnes entres sur cette page du formulaire
        this.localstockage.getData("idForm").then((val)=> {
          this.localstockage.getAllData().then((dataForm)=>{
            //il faut crer/mettre  jour le formulaire avec toutes les donnes stockes
            if (val==null){
              //Si le formulaire n'a pas t cr, il faut le crer
              this.formulaire.createForm(dataForm);  
            } else {
              //Sinon, il faut le mettre  jour
              this.formulaire.updateForm(dataForm);
            }
          });
        });
        if (this.therapiesAlterForm.controls.phytoForm.value || this.therapiesAlterForm.controls.vitamineForm.value || this.therapiesAlterForm.controls.aromaForm.value) {
          this.navCtrl.push(TraitementNom, {
            liste : this.traitementListe
          });
        } else {
          this.navCtrl.push(Aliments);
        }
      });
    }
  }
}


