import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

import { Api } from './api';
import { LocalStockage } from './localstockage';

/**
 * @class Cancer - Ce service utilise les requtes dfinies dans le fichier providers/api pour faire l'interface entre le client et le serveur (et la table Cancer).
 * Les rponses du serveur aux requtes envoyes sont des objets JSON, et doivent commencer par le champ `status` : 
 * ```json
 * {
 *   status: 'success',
 *   traitres: {
 *     // ce champ doit contenir a minima l'id du formulaire, stock sous le nom idForm.
 *   }
 * }
 * ```
 * Si le champ `status` n'est pas un `success`, une erreur est envoye.
 */

@Injectable()
export class Cancer {

  public subCreate: any;

  constructor(public http: Http, public api: Api, public localstockage: LocalStockage) { }

  /**
   * Mthode qui envoie une requte GET pour rcuprer la liste des traitements enregistrs dans la base ct serveur. 
   * @method getCancer
   * @requires providers/localstockage - la fonction utilise la mthode setData.
   * @requires providers/api - la fonction utilise la mthode get.
   * @param {} - aucun paramtre n'est pass  la mthode.
   * @returns {Observable} - un observable est renvoy pour suivre l'tat de la requte. 
   */
  getCancer() {
    let seq = this.api.get('cancers/').share();
    return seq;
  }

  /**
   * Fonction qui permet 
   * @method makeCancerList
   * @requires providers/cancer - elle appelle la mthode getCancer.
   * @param {} - aucun paramtre n'est pass  la fonction..
   * @returns {array} - un tableau avec les organes est retourne par la fonction.
   */
  makeCancerList(){

    return new Promise((resolve,reject) => {
      let organeListHemato = [];
      let organeTabHemato = [];
      let organeListOrgane = [];
      let organeTabOrgane = [];
      this.getCancer().toPromise().then((res) => {
        var auxTab = res.json().data;
        auxTab.forEach((element) => {
          if (element.hemato) {
            organeListHemato.push(element.nom.charAt(0).toUpperCase() + element.nom.slice(1).toLowerCase());
            organeTabHemato.push(element);
          } else {
            organeListOrgane.push(element.nom.charAt(0).toUpperCase() + element.nom.slice(1).toLowerCase());
            organeTabOrgane.push(element);
          }
        });
      }).catch((err)=>{
        console.error('ERROR', err);
      });
      resolve([organeListHemato,organeTabHemato,organeListOrgane,organeTabOrgane]);
    });
  } 
}


