import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Rx';
import {Idle, DEFAULT_INTERRUPTSOURCES} from '@ng-idle/core';
import { TranslateService } from '@ngx-translate/core';
import { AlertController } from 'ionic-angular';

import { Formulaire } from './formulaire';
import { LocalStockage } from './localstockage';

/**
 * @class Inactif - Ce service permet de vrifier si l'utilisateur est actif ou pas, et le cas chant de le renvoyer vers la page d'accueil du formulaire.
 */

@Injectable()
export class Inactif {

  idleCount: number = 60; //Fixe le temps avant que l'utilisateur, une fois inactif, soit redirig vers la page d'accueil.
  idleState: any;

  constructor(public alertCtrl: AlertController, public translate: TranslateService, private idle: Idle, public localstockage: LocalStockage, public formulaire: Formulaire) {
    // Temps  partir duquel on estime que l'utilisateur est inactif.
    idle.setIdle(1000);
    // Actions qui terminent l'inactivit.
    idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);
  }

  /**
   * Mthode qui lance la dtection de l'inactivit de l'utilisateur, et qui, une fois l'inactivit dtecte, ouvre une fentre d'alerte. 
   * @method idleSet
   * @requires providers/inactif - la fonction utilise la mthode idleRedirectConfirm.
   * @param {Controller, Controller} - les deux controlleurs, qui correspondent  la page sur laquelle l'utilisateur est prsent et  la fentre d'alerte sont passes  la mthode.
   * @returns {} - aucune valeur n'est retourne par la mthode.
   */
  idleSet(navCtrl) {
    this.idle.watch();
    this.idleState = this.idle.onIdleStart.subscribe(()=>{
      this.idleRedirectConfirm(navCtrl);
    });
  }

  /**
   * Mthode qui arrte la dtection de l'inactivit de l'utilisateur. 
   * @method idleStop
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la mthode.
   */
  idleStop() {
    this.idleState.unsubscribe();
    this.idle.stop();
    this.idle.ngOnDestroy();
  }

  /**
   * Mthode qui lance un timer.
   * @method idleTimer
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {Observable} - un observable est renvoy pour suivre l'tat du timer.
   */
  idleTimer(){  
    return Observable.timer(0,1000);
  }

  /**
   * Mthode qui ouvre une fentre d'alerte pour informer  l'utilisateur qu'il est inactif. 
   * L'utilisateur peut soit confirmer la redirection vers la redirection vers la page d'accueil, soit l'annuler, soit attendre qu'il soit redirig au terme d'un compte  rebours.
   * La redirection s'accompagne d'une suppression de toutes les donnes stockes localement.
   * @method idleRedirectConfirm
   * @requires providers/inactif - la fonction utilise la mthode idleSet, idleTimer.
   * @requires providers/localstockage - la fonction utilise la mthode clearAllData.
   * @param {Controller, Controller} - les deux controlleurs, qui correspondent  la page sur laquelle l'utilisateur est prsent et  la fentre d'alerte sont passes  la mthode.
   * @returns {} - aucune valeur n'est retourne par la mthode.
   */
  idleRedirectConfirm(navCtrl) {
    //Arrt de la dtection de l'inactivit de l'utilisateur
    this.idleState.unsubscribe();
    this.idle.stop();

    //Cration de la fentre d'alerte
    let buttonTextConfirm: string;
    let buttonTextCancel: string;
    this.translate.get('BOUTON_CONFIRM_ALERTE').subscribe(value => {
      buttonTextConfirm = value;
    });
    this.translate.get('BOUTON_ANNUL_ALERTE').subscribe(value => {
      buttonTextCancel = value;
    });

    let alert = this.alertCtrl.create({
      title: '',
      subTitle: '',
      message: '',
      cssClass: 'idleCss',
      buttons: [
        {
          text: buttonTextConfirm,
          handler: () =>{ 
            timer.unsubscribe();
            alert.dismiss().then(() => {
              this.localstockage.getData("idForm").then((val)=> {
                if (val!==null){
                  this.formulaire.removeForm(val);
                }
              });
              navCtrl.popToRoot();
            });
            return false;//La fermeture de l'alerte est faite manuellement, par alert.dismiss(), une fois la suppression des donnes effectues.
          }
        },
        {
          text: buttonTextCancel,
          role: 'cancel',
          handler: () =>{
            //Relance de la dtection de l'inactivit de l'utilisateur
            timer.unsubscribe();
            alert.dismiss().then(() => {
              this.idleSet(navCtrl);
            });
            return false;//La fermeture de l'alerte est faite manuellement, par alert.dismiss(), une fois la suppression des donnes effectues.
          }
        }
      ]
    });
    let titre: string;
    let message: string;
    this.translate.get('TITRE_ALERTE').subscribe(value => {
      titre = value;
    });
    this.translate.get('MESSAGE_ALERTE').subscribe(value => {
      message = value;
    });
    alert.setTitle(titre);
    alert.setMessage(message);
    //Cration d'un compte  rebours qui, une fois  zro, redirige l'utilisateur vers la page d'accueil du formulaire
    let timer = this.idleTimer().subscribe((t)=>{
      let count = this.idleCount-t;
      if (count>=0){
        let sousTitrePrinc: string;
        let sousTitreUnit: string;
        this.translate.get('SOUSTITRE_ALERTE').subscribe(value => {
          sousTitrePrinc = value;
        });
        this.translate.get('SOUSTITRE_UNIT_ALERTE').subscribe(value => {
          sousTitreUnit = value;
        });
        alert.setSubTitle(sousTitrePrinc + count + sousTitreUnit);
      } else {
        timer.unsubscribe();
        alert.dismiss().then(() => {
          this.localstockage.getData("idForm").then((val)=> {
            if (val!==null){
              this.formulaire.removeForm(val);
            }
          });
          navCtrl.popToRoot();
        });
      }
    });
    
    alert.present();
  }
}



