import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

import { Api } from './api';
import { LocalStockage } from './localstockage';

/**
 * @class Traitement - Ce service utilise les requtes dfinies dans le fichier providers/api pour faire l'interface entre le client et le serveur (et la table Traitement).
 * Les rponses du serveur aux requtes envoyes sont des objets JSON, et doivent commencer par le champ `status` : 
 * ```json
 * {
 *   status: 'success',
 *   traitres: {
 *     // ce champ doit contenir a minima l'id du formulaire, stock sous le nom idForm.
 *   }
 * }
 * ```
 * Si le champ `status` n'est pas un `success`, une erreur est envoye.
 */

@Injectable()
export class Traitement {

  public subCreate: any;

  constructor(public http: Http, public api: Api, public localstockage: LocalStockage) { }

  /**
   * Mthode qui envoie une requte GET pour rcuprer la liste des traitements enregistrs dans la base ct serveur. 
   * @method getTrait
   * @requires providers/api - la fonction utilise la mthode get.
   * @param {string} - le type de traitement est pass  la mthode.
   * @returns {Observable} - un observable est renvoy pour suivre l'tat de la requte. 
   */
  getTrait(type) {
    let seq = this.api.get('traitements/' + type).share();
    return seq;
  }

  /**
   * Fonction qui permet de faire un tableau avec les traitements.
   * @method makeTraitList
   * @requires providers/traitement - elle appelle la mthode getTrait.
   * @param {array} - un tableau de rfrence des traitements  slectionner est passe  la fonction.
   * @returns {array} - deux tableaux avec les traitements et leurs noms sont retourns par la fonction.
   */
  makeTraitList(refArray){
    return new Promise((resolve,reject) => {
      let traitementList = [];
      let traitementTab = [];
      refArray.forEach((reference) => {
        this.getTrait(reference).toPromise().then((res) => {
          var auxTab = res.json().data;
          auxTab.forEach((element) => {
            if (element.nom !== 'autres_phyto' && element.nom !== 'autres_ttcan') {
              traitementList.push(element.nom.charAt(0).toUpperCase() + element.nom.slice(1).toLowerCase());
              traitementTab.push(element);
            }
          });
        }).catch((err)=>{
          console.error('ERROR', err);
        });
      });
      resolve([traitementList,traitementTab]);
    });
  }
}


