import {Component, ViewChild} from '@angular/core';
import {ViewController, NavParams, Searchbar} from 'ionic-angular';

import { Diacritics } from '../../providers/diacritics';

@Component({
  selector: 'autocomplete',
  templateUrl: 'autocomplete.html'
})

export class Autocomplete {
  autocompleteItems: any;
  autocompleteData: any;
  autocompleteEntry: string;

  @ViewChild(Searchbar) searchbar:Searchbar;

  constructor (public viewCtrl: ViewController, public params: NavParams, public diacritics: Diacritics) {
    this.autocompleteItems = [];
    this.autocompleteData = this.params.get('dataAutocomplete');
    this.autocompleteEntry = this.params.get('entryAutocomplete');
  }

  ionViewDidEnter() {
    setTimeout(() => {
      this.searchbar.setFocus();
    });
  }

  /**
   * Fonction qui supprime la page modale ouverte, en passant comme valeur  la page initiale la valeur entre par l'utilisateur.
   * @method dismiss
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  dismiss() {
    this.viewCtrl.dismiss(this.autocompleteEntry);
  }

  /**
   * Fonction qui supprime la page modale ouverte, en passant comme valeur  la page initiale la valeur slectionne dans la liste propose (autocompletion).
   * @method chooseItem
   * @param {any} - la valeur slectionne dans la liste propose est passe  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  chooseItem(item: any) {
    this.autocompleteEntry = item;
    this.viewCtrl.dismiss(item);
  }

  /**
   * Fonction qui,  chaque saisie de l'utilisateur, compare le contenu saisi et la liste propose pour limiter la liste aux valeurs qui correspondent  la saisie : autocompletion.
   * @method updateSearch
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {string} - une liste mise  jour suite  la nouvelle saisie de l'utilisateur est retourne par la fonction.
   */
  updateSearch() {
    if (this.autocompleteEntry == '') {
      this.autocompleteItems = [];
    } else {
      let arrayLength: number = this.autocompleteData.length;
      var i: number = 0;
      var nbItems: number = 0;
      var table = [];
      while (i < arrayLength && nbItems < 20) {
        let strVal = this.diacritics.replaceDiacritics(this.autocompleteData[i].toLowerCase());
        let strEntry = this.diacritics.replaceDiacritics(this.autocompleteEntry.toLowerCase());
        if (strVal.indexOf(strEntry) > -1) {
          table.push(strVal);
          nbItems++;
        }
        i++;
      }
      this.autocompleteItems = table;
      /*this.autocompleteItems = this.params.get('dataAutocomplete').filter((val)=>{
        let strVal = this.diacritics.replaceDiacritics(val.toLowerCase());
        let strEntry = this.diacritics.replaceDiacritics(this.autocompleteEntry.toLowerCase());
        return strVal.indexOf(strEntry) > -1;
      });*/
    }
  }
}


