import { Injectable } from '@angular/core';
import { Http, RequestOptions, URLSearchParams } from '@angular/http';
import 'rxjs/add/operator/map';

/**
 * @class Api - Ce service prend en charge les requtes vers l'API REST ct client. Il faut, avant toute chose, dterminer l'URL de l'API.
 * Les donnes changes doivent tre en format JSON. Les donnes reues doivent toujours commencer par le champ 'status' :
 * ```json
 * {
 *   status: 'success',
 *   formres: {
 *     // ce champ doit contenir a minima l'id du formulaire, stock sous le nom idForm
 *   }
 * }
 * ```
 * Si le champ `status` n'est pas un `success`, une erreur est envoye.
 */

@Injectable()
export class Api {
  
  url: string = 'http://51.255.38.12/api/v1';

  constructor(public http: Http) {}

  /**
   * Mthode qui envoie une requte GET  l'API pour rcuprer des donnes. 
   * @method get
   */
  get(endpoint: string, params?: any, options?: RequestOptions) {
    if (!options) {
      options = new RequestOptions();
    }

    // Support easy query params for GET requests
    if (params) {
      let p = new URLSearchParams();
      for (let k in params) {
        p.set(k, params[k]);
      }
      // Set the search field if we have params and don't already have
      // a search field set in options.
      options.search = !options.search && p || options.search;
    }

    return this.http.get(this.url + '/' + endpoint, options);
  }

  /**
   * Mthode qui envoie une requte POST  l'API pour envoyer des donnes sur le serveur. 
   * Cette mthode est utilise lorsque l'URI pour accder  la ressource n'est pas encore cre et sera cre ct serveur. Les donnes envoyes seront donc dans un sous-domaine de l'URI utilise pour envoyer les donnes.
   * @method post
   */
  post(endpoint: string, body: any, options?: RequestOptions) {
    return this.http.post(this.url + '/' + endpoint, body, options);
  }

  /**
   * Mthode qui envoie une requte PUT  l'API pour envoyer des donnes sur le serveur. 
   * Cette mthode est utilise lorsque l'URI pour accder  la ressource est dtermine par le client : c'est l'URI utilise pour envoyer les donnes.
   * @method put
   */
  put(endpoint: string, body: any, options?: RequestOptions) {
    return this.http.put(this.url + '/' + endpoint, body, options);
  }

  /**
   * Mthode qui envoie une requte DELETE  l'API pour supprimer des donnes sur le serveur.
   * @method delete
   */
  delete(endpoint: string, options?: RequestOptions) {
    return this.http.delete(this.url + '/' + endpoint, options);
  }

  /**
   * Mthode qui envoie une requte PATCH  l'API pour envoyer des donnes sur le serveur. 
   * Cette mthode est utilise lorsque les donnes envoyes sont une mise  jour d'une ressource dj identifie. Les donnes correspondent donc  un format attendu.
   * @method patch
   */
  patch(endpoint: string, body: any, options?: RequestOptions) {
    return this.http.patch(this.url + '/' + endpoint, body, options);
  }
}



