import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { Accueil } from '../../accueil/accueil';

@Component({
  selector: 'refus-formulaire',
  templateUrl: 'refus-formulaire.html'
})
export class RefusFormulaire {

  timerCount: number = 15; //Fixe le temps avant que l'utilisateur soit redirig vers la page d'accueil.
  succesForm: boolean = true;

  constructor(public navCtrl: NavController, public navParams: NavParams) {}

  ngOnInit(){
    this.startTimer();
  }
    /**
   * Fonction qui permet de lancer le compte  rebours.
   * @method startTimer
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
    startTimer(){
      setTimeout(x => {
        if(this.timerCount <= 0) { 
          //Navigation  la page d'accueil du formulaire - Accueil
          this.navCtrl.push(Accueil);
        } else {
          this.timerCount -= 1;
          this.startTimer();
        }
      }, 1000);
    }

  /**
   * Fonction qui est lie au bouton "Retourner sur la page d'accueil" sur la page. 
   * Elle affiche la page d'accueil du formulaire - Accueil.
   * @method nextPage
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  nextPage() {
    //Navigation  la page d'accueil du formulaire - Accueil
    this.navCtrl.push(Accueil);
  }
}



