import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit, ViewChild, NgZone } from '@angular/core';
import { NavController, ModalController, Content } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { Keyboard } from '@ionic-native/keyboard';

//page suivante
import { Therapies } from '../therapies/therapies';

//page autocomplete
import{ Autocomplete } from '../../autocomplete/autocomplete';

//providers
import { Formulaire } from '../../../providers/formulaire';
import { LocalStockage } from '../../../providers/localstockage';
import { Cancer } from '../../../providers/cancer';
import { Diacritics } from '../../../providers/diacritics';
import { Inactif } from '../../../providers/inactif';
import { MaladieValidator } from '../../../providers/validators';

@Component({
  selector: 'maladie',
  templateUrl: 'maladie.html'
})
export class Maladie implements OnInit {

  @ViewChild(Content) content: Content;

  maladieForm: FormGroup;
  submitAttempt: boolean = false;
  organeNom: any;
  organeElement: any;
  hematoNom: any;
  hematoElement: any;
  organeTitre: string;
  organePlaceholder: string;
  hematoTitre: string;
  hematoPlaceholder: string;
  organeChoix: string;
  isHemato: boolean = false;
  isNotHemato: boolean = false;
  isOrgane: boolean = false;
  showScrollFabMaladie: boolean = false;
  contentDimensions: any;
  
  constructor(public navCtrl: NavController, public modalCtrl: ModalController, public zone: NgZone, public translate: TranslateService, public formBuilder: FormBuilder, public formulaire: Formulaire, public localstockage: LocalStockage, public organe: Cancer, public keyboard: Keyboard, public diacritics: Diacritics, public inactif: Inactif) {
    this.maladieForm = formBuilder.group({
      typeboolForm : ['', Validators.required],
      organeboolForm : [''],
      organeForm: ['', Validators.pattern('([A-Z ]{5})')],
      nom_organeForm: ['', Validators.pattern('([0-9a-zA-Z\u0152\u0153\\- \'\(\)]*)')],
      etatForm:  ['']
    },{ validator: MaladieValidator.isValid});
    this.organeNom = [];
    this.organeElement = [];
    this.hematoNom = [];
    this.hematoElement = [];
    this.contentDimensions = {};
  }

  ngOnInit(){
    //Ruperation des donnes (id et nom) de la table cancer
    this.organe.makeCancerList().then((liste) =>{
      this.hematoNom = liste[0];
      this.hematoElement = liste[1];
      this.organeNom = liste[2];
      this.organeElement = liste[3];
    });
    this.organeChoix = '';
  }

  ionViewDidEnter(){
    //Si l'utilisateur est inactif, une alerte est envoye avec la possibilit de continuer ou de recommencer le questionnaire.
    this.inactif.idleSet(this.navCtrl);
    this.contentDimensions = this.content.getContentDimensions();
    if (this.contentDimensions.contentHeight + 50 < this.contentDimensions.scrollHeight) {
      this.showScrollFabMaladie = true;
    }
  }

  ionViewWillLeave(){
    this.inactif.idleStop();
  }

  /**
   * Fonction qui permet d'afficher ou de cacher le boutton fab.
   * @method displayFab
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  displayFab(){
    this.zone.run(() => {
      this.contentDimensions = this.content.getContentDimensions();
      if (this.contentDimensions.contentHeight + 50 + this.contentDimensions.scrollTop < this.contentDimensions.scrollHeight) {
        this.showScrollFabMaladie = true;
      } else {
        this.showScrollFabMaladie = false;
      }
    });
  }

  /**
   * Fonction qui permet de scroller tout en bas du contenu.
   * @method scrollDownContent
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  scrollDownContent() {
    this.content.scrollToBottom();
  }

  /**
   * Fonction qui permet l'entre du type de cancer.
   * @method hematoOui()
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  hematoOui() {
    this.isHemato = true;
    this.isNotHemato = false;
    this.isOrgane = false;
    this.maladieForm.patchValue({organeForm: ''});
    this.maladieForm.patchValue({nom_organeForm: ''});
    this.organeChoix = '';
    this.showHematoModal();
  }

  /**
   * Fonction qui permet d'afficher de continuer le formulaire avec des cancers qui ne sont pas hmatologiques.
   * @method hematoNon()
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  hematoNon() {
    this.isHemato = false;
    this.isNotHemato = true;
    this.maladieForm.patchValue({organeForm: ''});
    this.maladieForm.patchValue({nom_organeForm: ''});
    this.organeChoix = '';
    //nouvelles dimensions de la fentre
    this.contentDimensions = this.content.getContentDimensions();
    if (this.contentDimensions.contentHeight + 50 < this.contentDimensions.scrollHeight) {
      this.showScrollFabMaladie = true;
    }
  }

  /**
   * Fonction qui permet l'entre du nom de l'organe primitif atteint.
   * @method organeOui
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  organeOui() {
    this.isHemato = false;
    this.isNotHemato = true;
    this.isOrgane = true;
    this.showOrganeModal();
    //nouvelles dimensions de la fentre
    this.contentDimensions = this.content.getContentDimensions();
    if (this.contentDimensions.contentHeight + 50 < this.contentDimensions.scrollHeight) {
      this.showScrollFabMaladie = true;
    }
  }

  /**
   * Fonction qui supprime l'entre du nom de l'organe primitif atteint.
   * @method organeNon
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  organeNon() {
    this.isHemato = false;
    this.isOrgane = false;
    this.maladieForm.patchValue({organeForm: 'SAITP'});
    this.maladieForm.patchValue({nom_organeForm: 'ne sais pas'});
    this.organeChoix = '';
  }

  /**
   * Fonction qui est lie au champ "Maladie hmatologique" sur la deuxime page du formulaire - Maladie.
   * Elle permet d'ouvrir une page modale (pages/autocomplete) qui propose, en fonction des entres de l'utilisateur une liste de noms possibles : autocompletion.
   * @method showHematoModal
   * @requires pages/autocomplete - elle appelle la page autocomplete.ts.
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  showHematoModal(){
    //Cration de la page modale en insrant les textes ncessaires
    this.translate.get('TITRE_MODAL_HEMATO').subscribe(value => {
      this.hematoTitre = value;
    });
    this.translate.get('PLACEHOLDER_MODAL_HEMATO').subscribe(value => {
      this.hematoPlaceholder = value;
    });
    //Cration de la page d'autocompletion
    let hematoModal = this.modalCtrl.create(Autocomplete, {entryAutocomplete: this.organeChoix, dataAutocomplete: this.hematoNom, titreAutocomplete: this.hematoTitre, placeholderAutocomplete: this.hematoPlaceholder});
    //Traitements lors de la fermeture de la page d'autocompletion
    hematoModal.onDidDismiss(data => {
      //Vrification que la donne passe existe et n'est pas seulement des espaces
      if (data && data.replace(/\s/g, '').length!=0){
        //Rcupration des donnes de la page autocompletion
        this.organeChoix = data;
        //Comparaison avec la table cancer
        var organeData = this.hematoElement.find((val)=>{
          let strVal = this.diacritics.replaceDiacritics(val.nom.toLowerCase());
          let strData = this.diacritics.replaceDiacritics(data.toLowerCase());
          if(strVal === strData){
            return val;
          }
        });
        //Attribution de valeurs aux champs du formulaire (organeForm et nom_organeForm)
        if(organeData){
          this.maladieForm.patchValue({organeForm: organeData.id});
          this.maladieForm.patchValue({nom_organeForm: organeData.nom});
        } else {
          this.maladieForm.patchValue({organeForm: 'AUCUN'});
          this.maladieForm.patchValue({nom_organeForm: this.organeChoix});
        }
      }
    });
    //Affichage de la page d'autocompletion
    hematoModal.present();
  }

  /**
   * Fonction qui est lie au champ "Organe primitif atteint" sur la deuxime page du formulaire - Maladie.
   * Elle permet d'ouvrir une page modale (pages/autocomplete) qui propose, en fonction des entres de l'utilisateur une liste de noms possibles : autocompletion.
   * @method showOrganeModal
   * @requires pages/autocomplete - elle appelle la page autocomplete.ts.
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  showOrganeModal(){
    //Cration de la page modale en insrant les textes ncessaires
    this.translate.get('TITRE_MODAL_ORGANE').subscribe(value => {
      this.organeTitre = value;
    });
    this.translate.get('PLACEHOLDER_MODAL_ORGANE').subscribe(value => {
      this.organePlaceholder = value;
    });
    //Cration de la page d'autocompletion
    let organeModal = this.modalCtrl.create(Autocomplete, {entryAutocomplete: this.organeChoix, dataAutocomplete: this.organeNom, titreAutocomplete: this.organeTitre, placeholderAutocomplete: this.organePlaceholder});
    //Traitements lors de la fermeture de la page d'autocompletion
    organeModal.onDidDismiss(data => {
      //Vrification que la donne passe existe et n'est pas seulement des espaces
      if (data && data.replace(/\s/g, '').length!=0){
        //Rcupration des donnes de la page autocompletion
        this.organeChoix = data;
        //Comparaison avec la table cancer
        var organeData = this.organeElement.find((val)=>{
          let strVal = this.diacritics.replaceDiacritics(val.nom.toLowerCase());
          let strData = this.diacritics.replaceDiacritics(data.toLowerCase());
          if(strVal === strData){
            return val;
          }
        });
        //Attribution de valeurs aux champs du formulaire (organeForm et nom_organeForm)
        if (organeData) {
          this.maladieForm.patchValue({organeForm: organeData.id});
          this.maladieForm.patchValue({nom_organeForm: organeData.nom});
        } else {
          this.maladieForm.patchValue({organeForm: 'AUCUN'});
          this.maladieForm.patchValue({nom_organeForm: this.organeChoix});
        }
      }
    });
    //Affichage de la page d'autocompletion
    organeModal.present();
  }

  /**
   * Fonction qui est lie au bouton "Continuer" sur la deuxime page du formulaire - Maladie.
   * Elle valide les valeurs entres dans les champs du formulaire et les stocke localement. 
   * Une fois ces valeurs stockes, elle rcupre la valeur stocke correspondant  l'identificant du formulaire. 
   * Si aucun identifiant n'a t stock, elle cr un nouveau formulaire avec toutes les donnes stockes. Sinon, elle met  jour le formulaire avec ces mmes donnes.
   * Elle affiche ensuite la troisime page du formulaire - Traitements Alternatifs.
   * @method nextPage
   * @requires providers/localstockage - la fonction utilise les mthodes setData, getData, getAllData.
   * @requires providers/formulaire - la fonction utilise les mthodes createForm et updateForm.
   * @param {} - aucun paramtre n'est pass  la fonction.
   * @returns {} - aucune valeur n'est retourne par la fonction.
   */
  nextPage() {
    //Boolen qui indique s'il faut ou non afficher les erreurs sur la page HTML
    this.submitAttempt = true;
    if(this.maladieForm.valid){
      //Stockage local des donnes remplies dans cette page de formulaire
      this.localstockage.setData(this.maladieForm.value).then((message) => {
        //Mise  jour/cration du formulaire sur le serveur avec les donnes entres sur cette page du formulaire
        this.localstockage.getData("idForm").then((val)=> {
          this.localstockage.getAllData().then((dataForm)=>{
            //il faut crer/mettre  jour le formulaire avec toutes les donnes stockes
            if (val==null){
              //Si le formulaire n'a pas t cr, il faut le crer
              this.formulaire.createForm(dataForm);
            } else {
              //Sinon, il faut le mettre  jour
              this.formulaire.updateForm(dataForm);
            }
          });
        });
        //Navigation  la page du formulaire - Thrapies
        this.navCtrl.push(Therapies);
      });
    }
  }
}


